<template>
    <d2-container>
        <!-- 商品信息 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="商品信息管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="10">
                    <!-- <el-button type="info" plain>查询</el-button> -->
                    <el-button type="primary" plain @click="add_good">添加</el-button>
                    <el-button type="success" plain @click="dialogVisible = true">导入商品信息</el-button>
                    <el-button type="warning" plain @click="land">导出商品信息</el-button>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="goods_title" label="商品名称" width="180">
            </el-table-column>
            <el-table-column prop="goods_desc" label="商品描述" width="180">
            </el-table-column>
            <el-table-column prop="goods_price" label="零售价--单位￥">
            </el-table-column>
            <el-table-column prop="goods_original_price" label="进价--单位￥">
            </el-table-column>
            <el-table-column label="操作">
                <template scope="val">
                    <el-button type="primary" plain @click="edit_val(val.row)">编辑</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="del_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加商品 -->
        <el-dialog :title="num" :visible.sync="centerDialogVisible" width="32%" center :before-close="clear">
            <el-form ref="form" :model="form" label-width="180px">
                <el-form-item label="商品名称">
                    <el-input v-model="form.goods_title" placeholder="请输入商品名称"></el-input>
                </el-form-item>
                <el-form-item label="商品描述">
                    <el-input v-model="form.goods_desc" placeholder="请输入商品描述"></el-input>
                </el-form-item>
                <el-form-item label="商品进价">
                    <el-input v-model="form.goods_price" placeholder="请输入商品进价"></el-input>
                </el-form-item>
                <el-form-item label="商品折扣价">
                    <el-input v-model="form.goods_original_price" placeholder="请输入商品折扣价"></el-input>
                </el-form-item>
                <el-form-item label="商品数量">
                    <el-input v-model="form.goods_count" placeholder="请输入商品数量"></el-input>
                </el-form-item>
                <el-form-item label="不发布/发布">
                    <el-select v-model="form.goods_is_publish" filterable placeholder="请选择" style="width: 100%;">
                        <el-option v-for="item in val" :key="item.id" :label="item.msg" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="供应商选择">
                    <el-select v-model="form.goods_supplier_id" filterable placeholder="请选择" style="width: 100%;">
                        <el-option v-for="item in supplierlist" :key="item.id" :label="item.supplier_name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="二级商品分类选择">
                    <el-select v-model="form.goods_type_id" filterable placeholder="请选择" style="width: 100%;">
                        <el-option v-for="item in second" :key="item.id" :label="item.goods_type_second_name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="商品图片选择">
                    <el-upload action="" list-type="picture-card" :on-change="handlePictureCardPreview" :auto-upload="false" :on-remove="handleRemove"
                        :limit="1" :on-exceed="file_false" :file-list="fileLists">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisibles">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>


                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onSubmit" v-if="num!=='编辑商品'">确 定</el-button>
                <el-button type="primary" @click="onSubmit_edit" v-if="num=='编辑商品'">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 导入商品信息 -->
        <el-dialog title="导入商品信息" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align: center;">
            <el-upload class="upload-demo" action="" :on-change="handlePreview" :limit="1" :on-exceed="file_false" :on-remove="handleRemove"
                :auto-upload="false" :file-list="fileList">
                <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            </el-upload>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitUpload">确 定</el-button>
            </span>
        </el-dialog>

    </d2-container>

</template>

<script>
    import { show_goods, add_goods, del_goods, import_goods, default_key, edit_goods, upload, export_goods, supplier, show_goods_type_second } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'commodity_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                checked: 0,
                val: [{
                    msg: "不发布",
                    id: "GOODS_NOT_PUBLISH"
                }, {
                    msg: "发布",
                    id: "GOODS_PUBLISH"
                }],
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    goods_title: '',//名称
                    goods_desc: '',//描述
                    goods_price: '',//进价
                    goods_original_price: "",//卖价
                    goods_is_publish: '',//发布
                    goods_count: "",//数量
                    goods_photo: "",//图片
                    goods_type_id: "",//二级分类
                    goods_supplier_id: ""//供应商

                },
                fileList: [],
                fileLists: [],
                supplierlist: [],
                second: [],
                dialogImageUrl: '',
                dialogVisibles: false
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            add_good() {//添加商品
                this.num = "添加商品";
                this.centerDialogVisible = true;
            },
            onSubmit() {//添加商品
                let par = qs.stringify(this.form);
                add_goods(par).then(res => {
                    this.msg(res)
                })
            },
            edit_val(a) {//编辑商品
                console.log(a);
                this.num = "编辑商品";
                this.form = {
                    goods_title: a.goods_title,//名称
                    goods_desc: a.goods_desc,//描述
                    goods_price: a.goods_price,//进价
                    goods_original_price: a.goods_original_price,//卖价
                    goods_is_publish: a.goods_is_publish,//发布
                    goods_count: a.goods_count,//数量
                    goods_photo: a.goods_photo,//图片
                    goods_type_id: a.goods_type_id,//二级分类
                    goods_supplier_id: a.goods_supplier_id,//供应商
                    id: a.id
                }
                this.fileLists = [{
                    name:"aaa",
                    url: this.host + a.goods_photo
                }]
                this.centerDialogVisible = true;
            },
            onSubmit_edit() {//编辑商品
                let par = {
                    goods_title: this.form.goods_title,//名称
                    goods_desc: this.form.goods_desc,//描述
                    goods_price: this.form.goods_price,//进价
                    goods_original_price: this.form.goods_original_price,//卖价
                    goods_is_publish: this.form.goods_is_publish,//发布
                    goods_count: this.form.goods_count,//数量
                    goods_photo: this.form.goods_photo,//图片
                    goods_type_id: this.form.goods_type_id,//二级分类
                    goods_supplier_id: this.form.goods_supplier_id,//供应商
                }

                let id = this.form.id;
                edit_goods(par, id).then(res => {
                    this.msg(res)
                })
            },
            land() {//下载商品信息
                window.location.href = this.host + '/api/goods/export';
            },
            del_val(a) {//删除商品
                console.log(a);
                del_goods(a.id).then(res => {
                    this.msg(res)
                })
            },
            page(a) {//分页
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            init_goods() {//初始化
                let par = {
                    page: this.pages,
                    limit: 10
                }
                show_goods(par).then(res => {
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_goods()
                    return
                }
                Myerror(res, this)
            },
            clear() {//清空
                this.centerDialogVisible = false;
                this.dialogVisible = false;
                this.form = {
                    goods_title: '',//名称
                    goods_desc: '',//描述
                    goods_price: '',//进价
                    goods_original_price: "",//卖价
                    goods_is_publish: '',//发布
                    goods_count: "",//数量
                    goods_photo: "",//图片
                    goods_type_id: "",//二级分类
                    goods_supplier_id: ""//供应商

                }
            },
            handleClose() {//清空
                this.dialogVisible = false;
                this.fileList = []
            },
            submitUpload() {//上传商品信息
                let param = new FormData()
                param.append('file', this.fileList[0].raw)
                // console.log(this.fileList[0].raw);

                import_goods(param).then(res => {
                    // console.log(res);
                    this.msg(res)
                })
            },
            handleRemove(file, fileList) {//删除文件
                // console.log(file, fileList);
                this.fileList = [];
                this.$set(this.form, "goods_photo", '')
            },
            handlePreview(file) {//选中的文件
                // console.log(file);
                this.fileList.push(file)
            },
            file_false() {
                this.$message({
                    message: "已超出最大上传数量！",
                    center: true,
                    type: 'error'
                });
                return
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                console.log(file.raw);
                if (file.raw.size > 1024 * 1024 * 2) {
                    this.$message({
                        message: "请选择小于2M的图片！",
                        center: true,
                        type: 'error'
                    });
                    return
                }
                let param = new FormData()
                param.append('image', file.raw)
                param.append('path', "goodsImage")
                upload(param).then(res => {
                    console.log(res.data);
                    this.dialogImageUrl = res.data.imagePath
                    this.$set(this.form, "goods_photo", res.data.imagePath)
                })
                // this.dialogImageUrl = file.url;
            }
        },
        mounted() {
            // console.log(this.host);
            supplier().then(res => {//供应商
                console.log(res);
                this.supplierlist = res.data.data
            })
            show_goods_type_second().then(res => {//获取二级分类
                console.log(res);
                this.second = res.data;
            })
            default_key().then(res => {//获取系统默认值
                console.log(res);

            })
            this.init_goods()
        },
        computed: {
            host() {
                return this.$store.state.d2admin.host
            }
        }
    }
</script>
<style lang="scss">
</style>